html {
  font-family: Arial, Helvetica, sans-serif;
}

body {
  margin: 0;
}

.box {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
}

.block {
  height: 300px;
  min-width: 300px;
  flex-basis: 100%;
  font-size: 100px;
  line-height: 300px;
  text-align: center;
  color: #fff;
  align-items: center;
  order: inherit;
}

.block--1 {
  background-color: red;
}

.block--2 {
  background-color: #c00;
}

.block--3 {
  background-color: #900;
}

.block--4 {
  background-color: #600;
}

.block--5 {
  background-color: #300;
}

.block--6 {
  background-color: #000;
}

@media (min-width: 900px) {

  .block {
    flex-basis: 33.33%;
  }

  .block--4 {
    order: 3;
  }
        
  .block--5 {
    order: 2;
  }
        
  .block--6 {
    order: 1;
  }

}

@media (min-width: 600px) and (max-width: 899px) {
    
  .block {
    flex-basis: 50%;
  }

  .block--3 {
    order: 2;
  }

  .block--4 {
    order: 1;
  }
        
  .block--5 {
    order: 3;
  }
        
  .block--6 {
    order: 4;
  }
}
